export default {
	state: {
		items: []
	},
	mutations: {
		//
		setItems(state, items) {
			state.items = [...state.items, items]
		},
		//
		clearItems(state) {
			state.items = []
		},
		deleteItems(state, items) {
			//
			const index = state.items.findIndex((index) => items._id === index._id)
			// 
			state.items.splice(index, 1)
		}
	}
}